.social-link {
  font-size: 1.8em;
  margin: 0 0.5em 0em 0.5em;
}
.icon-black {
  color: black;
}

.icon-black:hover {
  color: white;
}

.icon-white {
  color: white;
}

.icon-white:hover {
  color: black;
}

@media (min-width: 375px) {
  .social-link {
    font-size: 2.2em;
  }
}

@media (min-width: 650px) {
  .social-link {
    font-size: 2.5em;
  }
}



